/* muli-regular - latin */

@font-face {
  font-family: 'Muli';
  font-style: normal;
  font-weight: 400;
  src: local(''), url('../fonts/muli-v20-latin-regular.woff2') format('woff2'),  url('../fonts/muli-v20-latin-regular.woff') format('woff');
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* muli-900 - latin */

@font-face {
  font-family: 'Muli';
  font-style: normal;
  font-weight: 900;
  src: local(''), url('../fonts/muli-v20-latin-900.woff2') format('woff2'),  url('../fonts/muli-v20-latin-900.woff') format('woff');
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

.scroll-section {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #fff;
}

.hi {
  background: #8A2387;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F27121, #E94057, #8A2387);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.hi__title {
  max-width: 80%;
  font-size: calc(40px + 60 * (100vw / 1440));
}

.intro {
  background: #00F260;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0575E6, #00F260);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0575E6, #00F260);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.intro__title {
  font-size: calc(34px + 46 * (100vw / 1440));
  max-width: 80%;
}

.skills {
  flex-direction: column;
  justify-content: space-evenly;
  background: #FFE000;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #799F0C, #FFE000);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #799F0C, #FFE000);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.skills__card {
  height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font-size: 42px;
  font-weight: normal;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 30px;
}

.skills__card::before {
  content: '1';
  position: absolute;
  top: 50%;
  left: -20%;
  transform: translateY(-50%);
  font-size: 100px;
  font-weight: 900;
  color: rgba(255, 255, 255, 0.3);
}

.skills__card:nth-child(2)::before {
  content: '2';
  left: 90%;
  font-size: 200px;
}

.skills__card:nth-child(3)::before {
  content: '3';
  left: -10%;
  font-size: 150px;
}

.skills__card:nth-child(4)::before {
  content: '4';
  left: 100%;
  font-size: 400px;
}

.projects {
  flex-direction: column;
  justify-content: space-evenly;
}

.projects__title {
  color: #fff;
  text-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
  font-size: calc(34px + 46 * (100vw / 1440));
  max-width: 80%;
  z-index: 2;
}

.projects__wrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 0;
  transition: 0.5s;
  pointer-events: none;
  background-color: #E94057;
}

.project__casa,
.project__todo,
.project__react {
  width: 30vw;
  height: 30vh;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: #000;
  font-size: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  text-decoration: none;
  overflow: hidden;
  margin: 15px;
  padding: 10px;
}

.project__casa:hover,
.project__todo:hover,
.project__react:hover,
.project__casa:focus,
.project__todo:focus,
.project__react:focus,
.project__casa:active,
.project__todo:active,
.project__react:active,
.project__casa:hover + .bg,
.project__todo:hover + .bg,
.project__react:hover + .bg,
.project__casa:focus + .bg,
.project__todo:focus + .bg,
.project__react:focus + .bg,
.project__casa:active + .bg,
.project__todo:active + .bg,
.project__react:active + .bg {
  color: #fff;
  opacity: 1;
  background: #c31432;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #240b36, #c31432);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #240b36, #c31432);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.project__casa::before,
.project__todo::before,
.project__react::before {
  content: '';
  width: 100%;
  height: 100%;
  display: none;
  position: absolute;
  top: 0;
  left: -50%;
  background-color: rgba(255, 255, 255, 0.2);
  pointer-events: none;
}

.project__casa::after,
.project__todo::after,
.project__react::after {
  content: '</>';
  display: block;
  font-family: 'Muli';
  font-weight: 900;
  font-size: 100px;
  color: rgba(255, 255, 255, 0.1);
  position: absolute;
  bottom: 0;
  right: 0;
  filter: invert(1);
  pointer-events: none;
}

.project__casa:hover::before,
.project__todo:hover::before,
.project__react:hover::before {
  display: block;
}

.project__caption {
  margin: 0;
}

.project__todo:hover,
.project__todo:focus,
.project__todo:active,
.project__todo:hover + .bg,
.project__todo:focus + .bg,
.project__todo:active + .bg {
  background: #134E5E;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #71B280, #134E5E);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #71B280, #134E5E);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.project__todo::after {
  content: 'js';
}

.project__react {
  cursor: no-drop;
}

.project__react:hover,
.project__react:focus,
.project__react:active,
.project__react:hover + .bg,
.project__react:focus + .bg,
.project__react:active + .bg {
  background: #f7ff00;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #03A9F4, #e91e63);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #03A9F4, #e91e63);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*::before,
*::after {
  box-sizing: inherit;
}

*::selection {
  background-color: #bf2727;
  color: white;
}

figure {
  margin: 0;
}

input,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  font-family: 'CenturyGothic', sans-serif;
}

input:invalid {
  box-shadow: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input::-webkit-calendar-picker-indicator {
  color: transparent;
}

input::-webkit-clear-button {
  display: none;
}

select::-ms-expand {
  display: none;
}

textarea {
  overflow: hidden;
}

button {
  margin: 0;
  padding: 0;
  font-family: 'Aril', sans-serif;
}

section:focus,
div:focus {
  outline: none;
}

.iOS * {
  cursor: pointer;
}

body {
  overflow-x: hidden;
  font-family: 'Muli';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: white;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
}

@media screen and (max-width: 720px) {
  .project__casa,
  .project__todo,
  .project__react {
    height: 20vh;
    width: 70vw;
    font-size: 18px;
  }
}

@media screen and (max-width: 374px) {
  .project__casa,
  .project__todo,
  .project__react {
    font-size: 14px;
  }
}

@media screen and (max-width: 320px) {
  .hi__title {
    font-size: calc(40px + (60 + 60 * 0.7) * ((100vw - 320px) / 1440));
  }

  .intro__title {
    font-size: calc(34px + (46 + 46 * 0.7) * ((100vw - 320px) / 1440));
  }

  .projects__title {
    font-size: calc(34px + (46 + 46 * 0.7) * ((100vw - 320px) / 1440));
  }
}